import {
  arrow__left,
  arrow__right,
  bar,
  calendar,
  slider1,
  time_clock_circle,
  calendar__bttom,
  header__image__mobile,
  users,
  sheep,
  clandestins,
  car,
} from "src/assets/images";

import Slider2 from "../shared/Slider/slider2";
import { i18n } from "src/i18n";
import Image from "../shared/Image";
import { useEffect, useState } from "react";
// publications
import selectorsCategory from "src/modules/categoryPublication/list/categoryPublicationListSelectors";
import actionsPublication from "src/modules/publication/list/publicationListActions";
import selectorsPublication from "src/modules/publication/list/publicationListSelectors";
import actionsCategory from "src/modules/categoryPublication/list/categoryPublicationListActions";
import { Link } from "react-router-dom";

import { useDispatch, useSelector } from "react-redux";

// archives
import Archives from "src/view/Archive/Archives";
import { baseUrl } from "src/view/shared/BaseUrl";
import moment from "moment";

// glossaire
import AproposService from "src/modules/Apropos/AproposService";

// events
import actionsEvents from "src/modules/evenement/list/evenementListActions";
import selectorsvents from "src/modules/evenement/list/evenementListSelectors";
// calendar
import "src/view/shared/styles/dx.generic.tadeco-scheme-ftdes.css";
import { Scheduler, View } from "devextreme-react/scheduler";
import { useHistory } from "react-router-dom";

// stats

function Home() {
  const currentDate = new Date();
  const dispatch = useDispatch();
  const history = useHistory();
  const langue = localStorage.getItem("language");
  var localization = require("devextreme/localization");
  localization.locale(langue);
  const [apropos, setApropos] = useState("");
  const [eventData, setEventData] = useState([
    {
      id: "",
      text: "",
      startDate: new Date(),
      endDate: new Date(),
      allDay: true,
      img: "",
      description: "",
    },
  ]);

  const [stats, setStats] = useState({
    titleAR: "",
    titleFR: "",
    titleEN: "",
    descriptionAR: "",
    descriptionFR: "",
    descriptionEN: "",
    widgets: [
      {
        titleAR: "",
        titleFR: "",
        titleEN: "",
        descriptionAR: "",
        descriptionFR: "",
        descriptionEN: "",
      },
    ],
  });

  useEffect(() => {
    AproposService.list("", "", "", "").then((res) => {
      if (langue === "fr") {
        setApropos(res.rows[0].glossaireDescFR);
      }
      if (langue === "ar") {
        setApropos(res.rows[0].glossaireDescAR);
      }
      if (langue === "en") {
        setApropos(res.rows[0].glossaireDescEN);
      }
    });
    AproposService.listStats({ show: true }, "", "", "").then((res) => {
      if (res.count > 0) {
        setStats(res.rows[0]);
      }
    });
  }, []);

  useEffect(() => {
    dispatch(actionsPublication.doFetchValidePublication(4));
    dispatch(actionsCategory.doFetch());
    dispatch(actionsEvents.doFetch());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const rowsPublication = useSelector(selectorsPublication.selectRows);
  const selectRowsCategory = useSelector(selectorsCategory.selectRows);
  const rowsEvents = useSelector(selectorsvents.selectRows);

  const eventHeight =
    rowsEvents.length <= 3 ? 202 * rowsEvents.length + "px" : "606px";
  const eventGridTemplate =
    rowsEvents.length <= 3
      ? "202px".concat(
          " ",
          rowsEvents.length === 2
            ? "202px"
            : rowsEvents.length === 3
            ? "202px 202px"
            : ""
        )
      : "202px 202px 202px";
  const convertEventData = () => {
    const array = [
      {
        id: "",
        text: "",
        startDate: new Date(),
        endDate: new Date(),
        allDay: true,
        img: "",
        description: "",
      },
    ];
    rowsEvents.map((event) => {
      const data = {
        id: event.id,
        text:
          langue === "ar"
            ? event.titleAR
            : langue === "fr"
            ? event.titleFR
            : event.titleEN,
        description:
          langue === "ar"
            ? event.emplacementAR
            : langue === "fr"
            ? event.emplacementFR
            : event.emplacementEN,
        startDate: new Date(event.date),
        endDate: new Date(event.date),
        allDay: true,
        img: event.photos.length > 0 ? event.photos[0].downloadUrl : "",
      };
      array.push(data);
    });
    array.shift();
    setEventData(array);
  };
  useEffect(() => {
    convertEventData();
  }, [rowsEvents]);
  const Substring = (text, limit) => {
    if (text) {
      if (text.length > limit) {
        return text.substring(0, limit) + "...";
      } else return text;
    } else return text;
  };
  const renderAppointmentTooltip = (model) => {
    return (
      <Link to={`/Evenements/${model.appointmentData.id}`}>
        <div className="evenement__right" style={{ height: "300" }}>
          <h5 style={{ fontSize: 20 }}>{model.appointmentData.text}</h5>
          <p>
            <img
              src={model.appointmentData.img}
              style={{ marginTop: 10, height: "150px" }}
            />
          </p>
          <div className="right__footer">
            {Substring(model.appointmentData.description, 150)}
          </div>
        </div>
      </Link>
    );
  };
  const sliderTop = (obj) => {
    return (
      <div className="content__sliderTop">
        <div className="textContent">
          <div
            className="horizontalPlayerTitle ow"
            style={{
              color: "white",
              textShadow: "0px 4px 4px rgba(0, 0, 0, 0.25)",
              fontFamily: "Bebas Neue",
              fontStyle: "normal",
              fontWeight: 400,
              fontSize: "47px",
              lineHeight: "40px",
            }}
          >
            {langue === "ar"
              ? obj?.titleAR
              : langue === "fr"
              ? obj?.titleFR
              : obj?.titleEN}
          </div>
        </div>
      </div>
    );
  };

  const sliderBottom = (obj, index, slideIndex) => {
    return (
      <div className={slideIndex === index + 1 ? "active__slideBottom" : ""}>
        <span
          className=" lazy-load-image-background blur lazy-load-image-loaded"
          style={{ color: "transparent", display: "block" }}
        >
          <img
            style={{ width: 250, height: 100 }}
            alt="https://placehold.jp/204x175.png"
            src={obj?.photos?.length > 0 ? obj?.photos[0].downloadUrl : slider1}
          />
          <p
            className={
              slideIndex === index + 1 ? "text__p text__gras" : "text__p"
            }
          >
            {langue === "ar"
              ? Substring(obj?.titleAR, 30)
              : langue === "fr"
              ? Substring(obj?.titleFR, 30)
              : Substring(obj?.titleEN, 30)}
          </p>
        </span>
      </div>
    );
  };

  const renderPublications = (i) => {
    return (
      <div>
        <Link to={`/detail/${selectRowsCategory[0].publications[i + 1].id}`}>
          <Image
            className="lazyload"
            src={
              selectRowsCategory[0].publications[i + 1].photos.length > 0
                ? `${baseUrl}/file/download?privateUrl=${
                    selectRowsCategory[0].publications[i + 1].photos[0]
                      .privateUrl
                  }`
                : `https://placehold.jp/670x354.png`
            }
            alt="publication2 Icon"
          />
          <div className="list__detaill">
            <div className="detaill__header">
              <div className="header__left">
                <p>
                  {langue === "ar"
                    ? Substring(
                        selectRowsCategory[0].publications[i + 1].thematique
                          .titleAR,
                        30
                      )
                    : langue === "fr"
                    ? Substring(
                        selectRowsCategory[0].publications[i + 1].thematique
                          .titleFR,
                        30
                      )
                    : Substring(
                        selectRowsCategory[0].publications[i + 1].thematique
                          .titleEN,
                        30
                      )}
                </p>
              </div>
              <div className="header__right">
                {moment(selectRowsCategory[0].publications[i + 1].date).format(
                  "DD-MM-YYYY"
                )}
              </div>
            </div>
            <div className="detaill__content">
              {langue === "ar"
                ? Substring(
                    selectRowsCategory[0].publications[i + 1].titleAR,
                    30
                  )
                : langue === "fr"
                ? Substring(
                    selectRowsCategory[0].publications[i + 1].titleFR,
                    30
                  )
                : Substring(
                    selectRowsCategory[0].publications[i + 1].titleEN,
                    30
                  )}
            </div>
          </div>
        </Link>
      </div>
    );
  };
  return (
    <div className="app">
      {/* Styling the Header */}

      <div className="app__calendar">
        <Link to="/Evenements">
          <img src={calendar__bttom} alt="" />
        </Link>
      </div>
      <div className="app__header">
        <div className="header__content" style={{ height: 549 }}>
          <div className="content">
            <div className="content__imagePrincipale">
              <Slider2
                rows={rowsPublication}
                height={549}
                width={1170}
                label="slider"
                render={sliderTop}
                showDots={false}
                bottomrender={sliderBottom}
              />

              <img
                className="lazyload"
                src={header__image__mobile}
                id="image__mobile"
                alt="Header "
              />
            </div>
            {/* Slider Top */}

            {/* Slider Bottom  */}
          </div>
        </div>
      </div>

      {/* Styling the statestique */}
      <div className="app__statestique">
        <div className="statestique">
          <div className="archieve__header">
            <h2>{i18n("menu.Tableau")}</h2>
            <div className="satestique__bar" />
          </div>
          <div className="statestique__graph">
            {langue === "fr" ? (
              <iframe
                width="100%"
                height="700"
                src="https://app.powerbi.com/view?r=eyJrIjoiODQwODgxYjctMDgzNi00ODY1LWJmYTctNjgxOTVhODU4MTNhIiwidCI6ImEyYjI1MTExLWRmYzctNDNiMC1iNDYwLTJlMjc0ZDYyZTAzYiJ9"
                style={{ border: 0 }}
                allowFullScreen
              ></iframe>
            ) : (
              <iframe
                width="100%"
                height="700"
                src="https://app.powerbi.com/view?r=eyJrIjoiMmFlNDBmNmYtNjdkYy00ZjE2LWIwZGItNTFjYzkwY2NjNTA3IiwidCI6ImEyYjI1MTExLWRmYzctNDNiMC1iNDYwLTJlMjc0ZDYyZTAzYiJ9"
                style={{ border: 0 }}
                allowFullScreen
              ></iframe>
            )}
          </div>
          {stats ? (
            <div className="statesitique__global">
              <div className="global__left">
                <div className="global__header">
                  {langue === "ar"
                    ? stats.titleAR
                    : langue === "fr"
                    ? stats.titleFR
                    : stats.titleEN}
                </div>
                <div className="global__content">
                  {langue === "ar"
                    ? stats.descriptionAR
                    : langue === "fr"
                    ? stats.descriptionFR
                    : stats.descriptionEN}
                </div>
              </div>
              <div className="global__right">
                {stats.widgets[0] ? (
                  <div>
                    <div className="global__number">
                      {stats.widgets[0].titleAR}
                      <img src={users} alt="" />
                    </div>
                    <div className="detaill__number">
                      {langue === "ar"
                        ? stats.widgets[0].descriptionAR
                        : langue === "fr"
                        ? stats.widgets[0].descriptionFR
                        : stats.widgets[0].descriptionEN}
                    </div>
                  </div>
                ) : null}
                {stats.widgets[1] ? (
                  <div>
                    <div className="global__number">
                      {stats.widgets[1].titleAR}
                      <img src={sheep} alt="" />
                    </div>
                    <div className="detaill__number">
                      {langue === "ar"
                        ? stats.widgets[1].descriptionAR
                        : langue === "fr"
                        ? stats.widgets[1].descriptionFR
                        : stats.widgets[1].descriptionEN}
                    </div>
                  </div>
                ) : null}
                {stats.widgets[2] ? (
                  <div>
                    <div className="global__number">
                      {stats.widgets[2].titleAR}
                      <img src={clandestins} alt="" />
                    </div>
                    <div className="detaill__number">
                      {langue === "ar"
                        ? stats.widgets[2].descriptionAR
                        : langue === "fr"
                        ? stats.widgets[2].descriptionFR
                        : stats.widgets[2].descriptionEN}
                    </div>
                  </div>
                ) : null}
                {stats.widgets[3] ? (
                  <div>
                    <div className="global__number">
                      {stats.widgets[3].titleAR}
                      <img src={car} alt="" />
                    </div>
                    <div className="detaill__number">
                      {langue === "ar"
                        ? stats.widgets[3].descriptionAR
                        : langue === "fr"
                        ? stats.widgets[3].descriptionFR
                        : stats.widgets[3].descriptionEN}
                    </div>
                  </div>
                ) : null}
              </div>
              <div className="global__navigations">
                <div className="arrow__left">
                  <img
                    className="lazyload"
                    src={arrow__right}
                    alt="arrow__right ICon"
                  />
                </div>
                <div className="arrow__right">
                  <img
                    className="lazyload"
                    src={arrow__left}
                    alt="arrow__left Icon"
                  />
                </div>
              </div>
            </div>
          ) : null}
        </div>
      </div>
      {/* Styling the archeive */}
      <div
        className="archieve__header"
        style={{
          maxWidth: "1170px",
          marginLeft: "210px",
          marginRight: "210px",
        }}
      >
        <h2>{i18n("common.Archives")}</h2>
        <div className="publication__bar" />
      </div>
      <div className="archeive" style={{ maxWidth: "100%" }}>
        <div style={{ marginTop: 43 }}>
          <Archives acceuil={true} />
        </div>
      </div>
      {/* Stying the Publication */}
      {selectRowsCategory.length > 0 ? (
        <div className="app__publications">
          <div className="publication">
            {selectRowsCategory[0] ? (
              <div className="publication__left">
                <div className="archieve__header">
                  <h2>
                    {langue === "ar"
                      ? selectRowsCategory[0].titleAR
                      : langue === "fr"
                      ? selectRowsCategory[0].titleFR
                      : selectRowsCategory[0].titleEN}
                  </h2>
                  <div className="publication__bar" />
                </div>
                {selectRowsCategory[0].publications.length > 0 ? (
                  <div className="publication__images">
                    <Link
                      to={`/detail/${selectRowsCategory[0].publications[0].id}`}
                    >
                      <div className="images__header">
                        <div className="__header">
                          <Image
                            className="lazyload"
                            src={
                              selectRowsCategory[0].publications[0].photos
                                .length > 0
                                ? `${baseUrl}/file/download?privateUrl=${selectRowsCategory[0].publications[0].photos[0].privateUrl}`
                                : `https://placehold.jp/670x354.png`
                            }
                            alt="Publication Image"
                          />
                          <div className="__detaills">
                            <div className="list__detaill">
                              <div className="detaill__header">
                                <div className="__left">
                                  <p>
                                    {langue === "ar"
                                      ? selectRowsCategory[0].publications[0]
                                          .thematique.titleAR
                                      : langue === "fr"
                                      ? selectRowsCategory[0].publications[0]
                                          .thematique.titleFR
                                      : selectRowsCategory[0].publications[0]
                                          .thematique.titleEN}
                                  </p>
                                </div>
                                <div className="__right">
                                  {moment(
                                    selectRowsCategory[0].publications[0].date
                                  ).format("DD-MM-YYYY")}
                                </div>
                              </div>
                              <div className="__content">
                                {langue === "ar"
                                  ? Substring(
                                      selectRowsCategory[0].publications[0]
                                        .titleAR,
                                      30
                                    )
                                  : langue === "fr"
                                  ? Substring(
                                      selectRowsCategory[0].publications[0]
                                        .titleFR,
                                      30
                                    )
                                  : Substring(
                                      selectRowsCategory[0].publications[0]
                                        .titleEN,
                                      30
                                    )}
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </Link>
                    {selectRowsCategory[0].publications.length > 0 ? (
                      <div className="images__bottom">
                        {Array.from({
                          length:
                            (selectRowsCategory[0].publications.length < 4
                              ? selectRowsCategory[0].publications.length
                              : 4) - 1,
                        }).map((item, index) => renderPublications(index))}
                      </div>
                    ) : null}
                  </div>
                ) : null}
                <Link to={`/detaill/category/${selectRowsCategory[0]._id}`}>
                  <div className="plus__button">{i18n("common.voirPlus")}</div>
                </Link>
              </div>
            ) : null}
            {selectRowsCategory.length > 1 ? (
              <div className="publication__right">
                <div className="communique">
                  <div className="archieve__header">
                    <h2>
                      {langue === "ar"
                        ? selectRowsCategory[1].titleAR
                        : langue === "fr"
                        ? selectRowsCategory[1].titleFR
                        : selectRowsCategory[1].titleEN}
                    </h2>
                    <div className="communiquer__bar" />
                  </div>
                  {selectRowsCategory[1].publications.length > 0 ? (
                    <>
                      {Array.from({
                        length:
                          selectRowsCategory[1].publications.length <= 2
                            ? selectRowsCategory[1].publications.length
                            : 2,
                      }).map((item, i) => (
                        <div className="images__header">
                          <div className="__header">
                            <Link
                              to={`/detail/${selectRowsCategory[1].publications[i].id}`}
                            >
                              <Image
                                className="lazyload"
                                src={
                                  selectRowsCategory[1].publications[i].photos
                                    .length > 0
                                    ? `${baseUrl}/file/download?privateUrl=${selectRowsCategory[1].publications[i].photos[0].privateUrl}`
                                    : `https://placehold.jp/426x248.png`
                                }
                                alt="Publication Image"
                              />
                              <div className="__detaills">
                                <div className="list__detaill">
                                  <div className="detaill__header">
                                    <div className="__left">
                                      <p>
                                        {langue === "fr"
                                          ? selectRowsCategory[1].publications[
                                              i
                                            ].thematique.titleFR
                                          : selectRowsCategory[1].publications[
                                              i
                                            ].thematique.titleAR}
                                      </p>
                                    </div>
                                    <div className="__right">
                                      {" "}
                                      {moment(
                                        selectRowsCategory[1].publications[i]
                                          .date
                                      ).format("DD-MM-YYYY")}
                                    </div>
                                  </div>
                                  <div className="__content">
                                    {langue === "fr"
                                      ? Substring(
                                          selectRowsCategory[1].publications[i]
                                            .titleFR,
                                          30
                                        )
                                      : langue === "en"
                                      ? Substring(
                                          selectRowsCategory[1].publications[i]
                                            .titleEN,
                                          30
                                        )
                                      : Substring(
                                          selectRowsCategory[1].publications[i]
                                            .titleAR,
                                          30
                                        )}
                                  </div>
                                </div>
                              </div>
                            </Link>
                          </div>
                        </div>
                      ))}
                      <div className="communique">
                        <div className="">
                          <div className="publication__list">
                            {Array.from({
                              length:
                                (selectRowsCategory[1].publications.length < 6
                                  ? selectRowsCategory[1].publications.length
                                  : 6) - 2,
                            }).map((item, i) => (
                              <div>
                                <Link
                                  to={`/detail/${
                                    selectRowsCategory[1].publications[i + 2].id
                                  }`}
                                >
                                  <Image
                                    className="lazyload"
                                    src={
                                      selectRowsCategory[1].publications[i + 2]
                                        .photos.length > 0
                                        ? `${baseUrl}/file/download?privateUrl=${
                                            selectRowsCategory[1].publications[
                                              i + 2
                                            ].photos[0].privateUrl
                                          }`
                                        : `https://placehold.jp/120x80.png`
                                    }
                                    alt=""
                                    id="publication__leftImage"
                                  />
                                </Link>
                                <Link
                                  to={`/detail/${
                                    selectRowsCategory[1].publications[i + 2].id
                                  }`}
                                >
                                  <div className="list__detaill">
                                    <div className="detaill__header">
                                      <div className="header__left">
                                        <p>
                                          {langue === "ar"
                                            ? selectRowsCategory[1]
                                                .publications[i + 2].thematique
                                                .titleAR
                                            : langue === "fr"
                                            ? selectRowsCategory[1]
                                                .publications[i + 2].thematique
                                                .titleFR
                                            : selectRowsCategory[1]
                                                .publications[i + 2].thematique
                                                .titleEN}
                                        </p>
                                      </div>
                                      <div className="header__right">
                                        {moment(
                                          selectRowsCategory[1].publications[
                                            i + 2
                                          ].date
                                        ).format("DD-MM-YYYY")}
                                      </div>
                                    </div>
                                    <div className="detaill__content">
                                      {langue === "ar"
                                        ? Substring(
                                            selectRowsCategory[1].publications[
                                              i + 2
                                            ].titleAR,
                                            30
                                          )
                                        : langue === "fr"
                                        ? Substring(
                                            selectRowsCategory[1].publications[
                                              i + 2
                                            ].titleFR,
                                            30
                                          )
                                        : Substring(
                                            selectRowsCategory[1].publications[
                                              i + 2
                                            ].titleEN,
                                            30
                                          )}
                                    </div>
                                  </div>
                                </Link>
                              </div>
                            ))}
                          </div>
                        </div>
                        <Link
                          to={`/detaill/category/${selectRowsCategory[1]._id}`}
                        >
                          <div className="plus__button">
                            {i18n("common.voirPlus")}
                          </div>
                        </Link>
                      </div>
                    </>
                  ) : null}
                </div>
              </div>
            ) : null}
          </div>
        </div>
      ) : null}
      {/* Glossaire */}

      <div className="Glossaire">
        <h5>{i18n("menu.Glossaire")}</h5>
        <div className="glossaire__detaill">{apropos}</div>
        <Link to="/Glossaire">
          <div className="global__button">{i18n("common.discover")}</div>
        </Link>
      </div>

      {/* Styling the Evenement */}
      {rowsEvents.length > 0 ? (
        <div className="app__evenement">
          <div className="evenenement">
            <div className="archieve__header">
              <h2>{i18n("common.Evènements")}</h2>
              <div className="satestique__bar" />
            </div>
            <div
              className="evenement__content"
              style={{ width: "fit-content" }}
            >
              <div
                className="evenement__timing"
                style={{ height: eventHeight }}
              >
                <div className="timing__bar">
                  <img
                    className="lazyload"
                    src={bar}
                    alt=""
                    style={{ height: eventHeight }}
                  />
                </div>
                <div className="timing__number">
                  {Array.from({
                    length: rowsEvents.length < 3 ? rowsEvents.length : 3,
                  }).map((item, i) => (
                    <div>
                      <div className="cercle__red" />
                      <div className="time">
                        <div className="date">
                          {moment(rowsEvents[i].date).format("D")}
                        </div>
                        <div className="month">
                          {moment(rowsEvents[i].date)
                            .locale(langue === "ar" ? "ar" : "fr")
                            .format("MMMM")}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
              <div
                className="evenement__description"
                style={{
                  gridTemplateRows: eventGridTemplate,
                }}
              >
                {Array.from({
                  length: rowsEvents.length < 3 ? rowsEvents.length : 3,
                }).map((item, i) => (
                  <div>
                    <div className="evenement__left">
                      <Link to={`/Evenements/${rowsEvents[i]._id}`}>
                        <span
                          className=" lazy-load-image-background blur lazy-load-image-loaded"
                          style={{ color: "transparent", display: "contents" }}
                        >
                          <img
                            alt="https://placehold.jp/204x175.png"
                            src={
                              rowsEvents[i].photos.length > 0
                                ? `${baseUrl}/file/download?privateUrl=${rowsEvents[i].photos[0].privateUrl}`
                                : `https://placehold.jp/204x175.png`
                            }
                          />
                        </span>
                      </Link>
                    </div>
                    <Link to={`/Evenements/${rowsEvents[i]._id}`}>
                      <div className="evenement__right">
                        <div className="right__date">
                          <div>
                            <img
                              className="lazyload"
                              src={calendar}
                              alt=""
                              srcSet=""
                            />
                            {moment(rowsEvents[i].date).format("DD-MM-YYYY")}
                          </div>
                          <div>
                            <img
                              className="lazyload"
                              src={time_clock_circle}
                              alt=""
                            />
                            {moment(rowsEvents[i].date).format("LT")}
                          </div>
                        </div>
                        <div className="righ__desctiption">
                          {langue === "ar"
                            ? rowsEvents[i].titleAR
                            : langue === "fr"
                            ? rowsEvents[i].titleFR
                            : rowsEvents[i].titleEN}
                        </div>
                        <div className="right__footer">
                          {langue === "ar"
                            ? rowsEvents[i].emplacementAR
                            : langue === "fr"
                            ? rowsEvents[i].emplacementFR
                            : rowsEvents[i].emplacementEN}
                        </div>
                      </div>
                    </Link>
                  </div>
                ))}
              </div>
              <div className="evenement__calendar">
                <Scheduler
                  rtlEnabled={langue === "ar"}
                  views={[]}
                  onAppointmentDblClick={(e) => {
                    history.push(`/Evenements/${e.appointmentData.id}`);
                  }}
                  appointmentTooltipRender={renderAppointmentTooltip}
                  timeZone="France/Paris"
                  dataSource={eventData}
                  defaultCurrentView="month"
                  defaultCurrentDate={currentDate}
                  height={600}
                  firstDayOfWeek={1}
                  startDayHour={8}
                  endDayHour={18}
                ></Scheduler>
              </div>
            </div>
          </div>
        </div>
      ) : null}
    </div>
  );
}

export default Home;
